.menu-bar{
    background: $color1;
    display: flex;
    align-items: center;
    padding: .75em 1em 1.25em;
    @include media("phone", max){
        font-size: .75em;
    }
    & > *:not(:first-child){
        margin-left: .5em;
    }
    input[type="radio"]{
        display: none;
        & + label {
            display: flex;
            align-items: center;
            &:after{
                content: '';
                display: inline-block;
                background-size: cover;
                width: 3em;
                height: 3em;
            }
        }
        &:checked{
            & + label{
                background: $color5;
                box-shadow: 0 .25em darken($color5, 15%);
                transform: translateY(.25em);
                color: $color9;
            }
        }
        &#select+label:after{
            background-image: url('../images/click.svg');
        }
        &#diaper+label:after{
            background-image: url('../images/diaper.svg');
        }
    }
}
.help-btn{
    display: flex;
    align-items: center;
    margin-left: auto !important;
    &:before{
        content: '';
        display: inline-block;
        background-image: url('../images/help.svg');
        background-size: cover;
        width: 3em;
        height: 3em;
        margin-right: .5em;
    }
}