*{
    outline: none;
    box-sizing: border-box;
}

body,
html {
    font-family: $secondary-font-family;
    background-color: $background-color;
    color: $text-color;
    width: 100%;
    height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
dl,
ol,
ul,
dd,
dt,
li {
    margin: 0;
    padding: 0;
}

@each $element,
$size in $default-font-size {
    #{$element} {
        font-size: $size;
    }
}

@each $element,
$line-height in $default-line-height {
    #{$element} {
        line-height: $line-height;
    }
}

@each $element,
$margin-bottom in $default-margin-bottom {
    #{$element} {
        margin-bottom: $margin-bottom;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $primary-font-family;
    color: $text-color;
}

p,
blockquote {
    hyphens: auto;
}

strong {
    font-weight: bold;
}

em {
    font-style: italic;
}

ul {
    list-style-type: disc;
}

ol {
    list-style-type: decimal;
}

ul,
ol{
    margin-left: 2em;
}

blockquote{
    font-style: italic;
    border-left: .5em solid $text-color;
    padding-left: 1em;
}
img {
    max-width: 100%;
    margin-bottom: 1em;
}

.center{
    display: block;
    margin-left: auto;
    margin-right: auto;
}