.modal {
    display: none;
    position: fixed;
    z-index: z("venusaur");
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    padding: 2em;
    overflow: auto;
    @include media("phone", max){
        padding: 1em;
    }
    background-color: rgba($color1, .85); 
    &#modal-with-win-modal-content{
        background-color: rgba($color5, .85);
        .modal-content {
            text-align: center;
        }
    }
    &#modal-with-lose-modal-content{
        background-color: rgba($color2, .85);
        .modal-content {
            text-align: center;
        }
    }
    &#modal-with-baby-modal-content{
        .modal-content{
            text-align: center;
        }
    }
}

.modal-content {
    border-radius: 2em;
    background-color: $background-color;
    margin: auto;
    padding: 2em;
    max-width: 960px;
    @include media("phone", max){
        border-radius: 1em;
        padding: 1em;
        font-size: .85em;
    }
}

.close {
    color: rgba($text-color, .75);
    float: right;
    font-size: 2em;
    font-weight: bold;
    margin-left: .5em;
    margin-bottom: .5em;
    &:hover,
    &:focus {
        color: $text-color;
        text-decoration: none;
        cursor: pointer;
    }
}

.baby{
    position: relative;
    max-width: 320px;
    margin: 0 auto 1em;
    display: block;
    pointer-events: none;
    &:before{
        content: '';
        width: 88%;
        height: 100%;
        border-radius: 100%;
        background: $color1;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    img{
        position: relative;
        display: block;
        margin: auto;
        width: 100%;
    }
    &.boy{
        &:before{
            background: $color5;
        }
    }
    &.girl{
        &:before{
            background: $color7;
        }
    }
}