
.btn-wrapper{
    padding-bottom: 1em;
}
.btn {
    display: inline-flex;
    padding: .5em 1em;
    font-size: 1em;
    text-align: center;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    color: $text-color;
    background-color: $background-color;
    border: none;
    border-radius: 1em;
    box-shadow: 0 .5em darken($background-color, 10%);
    font-family: $primary-font-family;
    &:hover {
        background-color: darken($background-color, 2.5%);
        box-shadow: 0 .5em darken($background-color, 15%);
    }
    &:active {
      background-color: darken($background-color, 5%);
      box-shadow: 0 .25em darken($background-color, 15%);
      transform: translateY(.25em);
    }
    &--large{
        font-size: 1.25em;
        padding: 1em;
    }
    @function set-color($color, $alpha) {  
        @if (lightness( $color ) > 49) {
          @return rgba(black, $alpha);
        }
        @else {
          @return rgba(white, $alpha);
        }
    }
    @mixin buttonColors($id, $color){
        &--color#{$id}{
            background-color: $color;
            box-shadow: 0 .5em darken($color, 10%);
            color: set-color($color, 1);
            &:hover{
                background-color: darken($color, 2.5%);
                box-shadow: 0 .5em darken($color, 15%);
            }
            &:active{
                background-color: darken($color, 5%);
                box-shadow: 0 .25em darken($color, 15%);
            }

        }
    }
    @each $id, $data in $nearby {
        @include buttonColors($id, $data);
    }
}
  