.block {
    position: relative;
    @include responsive-ratio(1,1, true);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    border-radius: .5em;
    background: $background-color;
    border: 1px solid $background-color;
    color: $background-color;
    box-shadow: inset 4px 4px 0px 0px darken($background-color, 10%), inset -4px -4px 0px 0px darken($background-color, 20%);
    transition: all 150ms ease-in-out;
    &:after{
        @include centered;
        font-size: 1.75em;
        font-family: $primary-font-family;
    }
    &:hover{
        transform: scale(1.1);
        z-index: z("bulbasaur");
    }
    &.revealed {
        background-color: darken($background-color, 5%);
        box-shadow: none;
        pointer-events: none;
    }
    @mixin nearbyStyle($id, $color){
        &.nearby-#{$id}{
            color: $color;
            background: lighten($color, 40%);
            &:after{
                content: '#{$id}';
            }
        }
    }
    @each $id, $data in $nearby {
        @include nearbyStyle($id, $data);
    }
    &.flag {
        background: $color5;
        box-shadow: none;
        &:after{
            content: '';
            width: 1.5em;
            height: 1.5em;
            background-image: url("../images/diaper.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }
    &.mine {
        background: $color8;
        box-shadow: none;
        pointer-events: none;
        &:after{
            content: '';
            width: 1.5em;
            height: 1.5em;
            background-image: url("../images/poo.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }
}